import React, { useContext } from "react";
import { Link } from "react-router-dom";
import { ListGroup, ListGroupItem, Button } from "reactstrap";
import { Context } from "../context/Context";

export const UserList = () => {
  const { users, removeUser } = useContext(Context);
  return (
    <ListGroup className="container p-0 mt-3">
      {users.map((user) => (
        <ListGroupItem key={user.id} className="d-flex justify-content-between">
          <span>{user.username}</span>
          <div>
            <Link to={`/edit/${user.id}`} className="btn btn-warning">
              Edit
            </Link>
            <Button className="btn btn-danger ml-2" onClick={() => removeUser(user.id)}>Delete</Button>
          </div>
        </ListGroupItem>
      ))}
    </ListGroup>
  );
};
